import { useEffect, useState } from "react";
import { Routes, Route, Navigate } from "react-router-dom";
import { Header } from "@/components/layout/Header";
import { WalletsManagement } from "@/components/merchant/WalletsManagement";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
// تم إزالة trialService
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Clock, AlertTriangle } from "lucide-react";

export default function MerchantDashboard() {
  const { user, profile, loading } = useFirebaseAuth();

  useEffect(() => {
    if (!loading && (!user || !profile || profile.role !== 'merchant')) {
      window.location.href = '/auth';
    }
  }, [user, profile, loading]);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-background">
        <div className="text-center">
          <div className="h-8 w-8 animate-spin border-4 border-primary border-r-transparent rounded-full mx-auto mb-4"></div>
          <p className="text-muted-foreground">جاري التحميل...</p>
        </div>
      </div>
    );
  }

  if (!user || !profile || profile.role !== 'merchant') {
    return null;
  }

  // السماح بالدخول حتى إذا لم تكن الموافقة مكتملة
  if (profile && !profile.approved) {
    // لا تحويل لصفحة انتظار الموافقة
  }

  return (
    <div className="min-h-screen bg-background">
      <Header />
      <main className="container mx-auto p-6">
        <Routes>
          <Route path="/" element={<Navigate to="/merchant/wallets" replace />} />
          <Route path="/wallets" element={<WalletsManagement />} />
        </Routes>
      </main>
    </div>
  );
}
